.create header {
  border-bottom: 1px #000 solid;
  font-size: 1.2em;
  height: 20px;
  padding: 20px;
  width: 100%;
}

.create .steps {
  border-bottom: 1px #000 solid;
  font-size: 1em;
}

.create .steps .detail {
  display: none;
}

.create .steps.step1 .step1,
.create .steps.step2 .step2,
.create .steps.step3 .step3 {
  font-size: 1.3em;
}

.create .steps.step1 .step1 .detail,
.create .steps.step2 .step2 .detail,
.create .steps.step3 .step3 .detail {
  display: block;
}

.create #body {
  padding: 20px;
}

.create ul li {
 padding: 5px 10px;
}

.create .step .video {
  background: black;
  box-sizing: border-box;
  display: none;
  height: 100%;
  left: 0;
  overflow: hidden;
  position: absolute;
  text-align: center;
  top: 0;
  width: 100%;
}

.create .step.video .video {
  display: block;
}

.create .step .video .controls {
  bottom: 0;
  height: 50px;
  left: calc(50% - 60px);
  position: absolute;
  width: 120px;
}

.create canvas,
.create .step .video video {
  height: 100%;
  -moz-transform: scale(-1, 1);
  -webkit-transform: scale(-1, 1);
  -o-transform: scale(-1, 1);
  transform: scale(-1, 1);
  filter: FlipH;
}
